body {
  display: flex;
  padding-left: 50px;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background-color: #047230;
}

.birthdayCard {
  position: relative;
  width: 250px;
  height: 350px;
  transform-style: preserve-3d;
  transform: perspective(2500px);
  transition: 1s;
}

.birthdayCard:hover {
  transform: perspective(2500px) rotate(5deg);
  box-shadow: inset 100px 20px 100px rgba(0, 0, 0, 0.2),
    0 10px 100px rgba(0, 0, 0, 0.5);
}

.birthdayCard:hover .cardFront {
  transform: rotateY(-160deg);
}

.birthdayCard:hover .happy {
  visibility: hidden;
}

.cardFront {
  position: relative;
  background-color: #ad1a1a;
  width: 250px;
  height: 350px;
  overflow: hidden;
  transform-origin: left;
  /* box-shadow: inset 100px 20px 100px rgba(0, 0, 0, 0.2),
    30px 0 50px rgba(0, 0, 0, 0.4); */
  transition: 0.6s;
}

.happy {
  font-family: Tahoma, sans-serif;
  text-align: center;
  margin: 20px;
  background: linear-gradient(140deg, #940909 10%, #f2b7b7 25%, #940909 30%, #f2b7b7 45%, #940909 50%, #f2b7b7 65%, #940909 70%, #f2b7b7 85%, #940909 90%);
  transition: 0.1s;
  backface-visibility: visible;
  color: #fcf3e7;
  border-radius: 15px;
}

.cardInside {
  position: absolute;
  background-color: #9b0b0b;
  width: 250px;
  height: 350px;
  z-index: -1;
  left: 0;
  top: 0;
  box-shadow: inset 100px 20px 100px rgba(127, 7, 7, 0.85);
}

p {
  font-family: "Arial";
  margin: 30px;
  color: #f5e3c8;
  font-size: 15px;
}

.name {
  color: #f5e3c8;
  font-size: 15px;
}

.back {
  font-family: Tahoma, sans-serif;
  color: #f5e3c8;
  text-align: center;
  margin: 30px;
  outline-color: #f5e3c8;
  outline-style: dotted;
  border-radius: 25px;
}

.hover {
  color: rgb(245, 235, 235);
  padding-top: 45px;
  text-align: center;
}

.show-other-card {
  position: absolute;
  display: flex;
  top: 45rem;
  align-items: center;
  justify-content: center;
  text-align: center;
  background-color: #9d0909;
  color: #f5e3c8;
  border-color: #7b0202;
  border-radius: 30px;
  font-size: 20px;
  max-width: 15rem;
  width: 15rem;
  min-height: 4.44rem;
}

.dog-name {
  color: #f5e3c8;
  font-size: 15px;
}

@mixin transform($info) {
  transform: $info;
  -webkit-transform: $info;
  -moz-transform: $info;
  -ms-transform: $info;
}
@mixin gradient($type, $info) {
  background: #{$type}-gradient($info);
  background: -webkit-#{$type}-gradient($info);
  background: -moz-#{$type}-gradient($info);
  background: -ms-#{$type}-gradient($info);
}
@mixin browsers($info) {
  animation: $info;
  -webkit-animation: $info;
  -moz-animation: $info;
  -ms-animation: $info;
  -o-animation: $info;
}
@mixin blur($info) {
  filter: blur($info);
  -webkit-filter: blur($info);
  -moz-filter: blur($info);
  -ms-filter: blur($info);
  -o-filter: blur($info);
}
@mixin animation($name) {
  @keyframes #{$name} {
    @content;
  }
  @-webkit-keyframes #{$name} {
    @content;
  }
  @-moz-keyframes #{$name} {
    @content;
  }
  @-ms-keyframes #{$name} {
    @content;
  }
}

