}

.tree {
  position: absolute;
  text-align: center;
  width: 245px;
  &:before {
    background-color: $brown;
    content: '';
    height: 25px;
    left: 0;
    margin: auto;
    position: absolute;
    right: 0;
    top: 230px;
    width: 30px;
  }
}

.tree-piece {
