@include animation(lightItUp) {
  0% {
    background-color: $bulb-blue;
    box-shadow: 0px 0px 5px $bulb-blue;
  }
 24% {
    background-color: $bulb-blue;
    box-shadow: 0px 0px 5px $bulb-blue;
  }
  25% {
    background-color: $bulb-red;
    box-shadow: 0px 0px 5px $bulb-red;
  }
  49% {
    background-color: $bulb-red;
    box-shadow: 0px 0px 5px $bulb-red;
  }
  50% {
    background-color: $bulb-green;
    box-shadow: 0px 0px 5px $bulb-green;
  }
  74% {
    background-color: $bulb-green;
    box-shadow: 0px 0px 5px $bulb-green;
  }
  75% {
    background-color: $bulb-yellow;
    box-shadow: 0px 0px 5px $bulb-yellow;
  }
  100% {
